.slick-list {
  overflow: visible;
}

.slick-track {
  display: flex;
  gap: 16px;
}

.slick-small-prev,
.slick-small-next {
  position: absolute;

  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 32px;
  height: 32px;

  background-color: transparent;
  border: none;

  cursor: pointer;
}

.slick-small-prev {
  bottom: -52px;
  left: 0px;
}

.slick-small-next {
  bottom: -52px;
  left: 64px;
}

.slick-arrow {
  fill: var(--primary-dark);

  transition: fill var(--time) var(--cubic);

  @media screen and (min-width: $desktop) {
    &:hover,
    &:focus {
      fill: var(--secondary);
    }
  }
}

.slick-slide.room-gallery-slider__item {
  opacity: 0;
  transition: opacity 250ms linear;
}

.slick-slide.slick-active.room-gallery-slider__item {
  opacity: 1;
}
