:root {
  --primary-dark: #000;
  --primary-light: #fff;
  --secondary: #f3c653;
  --secondary-dark: #c79002;
  --text-primary: #7a7a7a;
  --text-secondary: #5f5f5f;
  --text-secondary-light: #b4b4b4;
  --text-mimino-dark: #494848;
  --text-mimino-light: #828282;
  --text-address: #e5e5e5;
  --primary-dark-rgb: 0, 0, 0;
  --secondary-rgb: 243, 198, 83;
  --bg-image-secondary-rgb: 186, 186, 186;
  --bg: #fffdee;
  --bg-dark: #f0ecd6;
  --bg-footer: #0e0c07;
  --bg-image: #dadada;
  --bg-image-secondary: #bababa;
  --time: 350ms;
  --cubic: linear;
}

body {
  color: var(--primary-dark);
  background-color: var(--bg);
  font-family: Montserrat, sans-serif;
  line-height: 1.25;
}

.container {
  max-width: 414px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 1024px) {
  .container {
