    padding-right: 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-open {
  overflow: hidden;
}

.complex__text {
  color: var(--text-secondary-light);
  margin-bottom: 12px;
  font-family: Times New Roman, Times, serif;
  font-size: 20px;
  line-height: 1.15;
  display: block;
}

