.slick-list {
  overflow: visible;
}

.slick-track {
  gap: 16px;
  display: flex;
}

.slick-small-prev, .slick-small-next {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
}

.slick-small-prev {
  bottom: -52px;
  left: 0;
}

.slick-small-next {
  bottom: -52px;
  left: 64px;
}

.slick-arrow {
  fill: var(--primary-dark);
  transition: fill var(--time) var(--cubic);
}

@media screen and (min-width: 1280px) {
  .slick-arrow:hover, .slick-arrow:focus {
    fill: var(--secondary);
  }
}

.slick-slide.room-gallery-slider__item {
  opacity: 0;
  transition: opacity .25s linear;
}

.slick-slide.slick-active.room-gallery-slider__item {
