.modal {
  width: 300px;
  z-index: 52;
  height: 480px;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: #fffdc0;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 30px;
  left: 50%;
  overflow: scroll;
  transform: translateX(-50%);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 500px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 750px;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.modal1.active, .modal2.active, .modal3.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.modal__button {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

.modal__icon {
  width: 40px;
  height: 40px;
  fill: #000;
}

.modal__button:focus .modal__icon, .modal__button:active .modal__icon {
  fill: #7eb101;
}

.modal__text {
  letter-spacing: .06em;
  font-size: 44px;
  line-height: 1.5;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 41;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .33);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

